import React from "react"

import Layout from "../components/Layout"
import "../styles/global.css"
import Product from "../components/Product"
import * as styles from "../styles/index.module.css"
import { graphql, Link } from "gatsby"
const products = [
  {
    category: "saffron",
    name: "Saffron Loose",
    origin: "Afghanistan",
    price: 200,
    unit: "gram",
    thumb: `../images/thumbnails/spices/all-seasoning.png`,
    image: "",
  },
  {
    category: "saffron",
    name: "Saffron Loose",
    origin: "Afghanistan",
    price: 200,
    unit: "gram",
    thumb: "/full-size/spices/all-seasoning.png",
    image: "",
  },
  {
    category: "saffron",
    name: "Saffron Loose",
    origin: "Afghanistan",
    price: 200,
    unit: "gram",
    thumb: "/icons/saffron.png",
    image: "",
  },
  {
    category: "saffron",
    name: "Saffron Loose",
    origin: "Afghanistan",
    price: 200,
    unit: "gram",
    thumb: "/icons/saffron.png",
    image: "",
  },
]

function filterByCategory(products, category) {
  return products.filter(product => product.category.toLowerCase() === category)
}

function getFirstFive(data) {
  return data.slice(0, 5)
}

export default function Home({ data }) {
  const saffrons = getFirstFive(
    filterByCategory(data.allDataJson.nodes, "saffron")
  )
  const driedFruits = getFirstFive(
    filterByCategory(data.allDataJson.nodes, "dried fruits")
  )
  const spices = getFirstFive(
    filterByCategory(data.allDataJson.nodes, "spices")
  )

  return (
    <Layout>
      <section className={`container`}>
        <div className={styles.hero}>
          <h1 className="h1">
            Don't miss world's best saffron at a great price.
          </h1>
          <p className="subtitle">
            Visit us today at{" "}
            <span className="address">
              Auburn Arcade, 12/75-77 Auburn Rd, Auburn NSW 2144
            </span>
          </p>
        </div>
      </section>
      <section className={`container ${styles.banners}`}>
        <div>
          <h3 className={styles.bannerText}>
            Top Quality Saffron from Afghanistan
          </h3>
        </div>
        <div>
          <h3 className={styles.bannerText}>Variety of Top Quality Spices</h3>
        </div>
        <div>
          <h3 className={styles.bannerText}>
            Variety of Top Quality Dried Fruits
          </h3>
        </div>
      </section>
      <section className={`container ${styles.products}`}>
        <h3>Our Products</h3>
        <div className={styles.rows}>
          <div className={styles.row}>
            <h3 className={styles.rowTitle}>Saffron</h3>
            {saffrons.map(product => (
              <Product
                category={product.category}
                name={product.name}
                origin={product.origin}
                price={product.price}
                unit={product.unit}
                thumb={product.thumb}
                slug={product.slug}
              />
            ))}
            <div className={styles.viewAllContainer}>
              <Link to="/saffron" className={styles.viewAll}>
                View All
              </Link>
            </div>
          </div>
          <div className={styles.row}>
            <h3 className={styles.rowTitle}>Dried Fruits</h3>
            {driedFruits.map(product => (
              <Product
                category={product.category}
                name={product.name}
                origin={product.origin}
                price={product.price}
                unit={product.unit}
                thumb={product.thumb}
                slug={product.slug}
              />
            ))}
            <div className={styles.viewAllContainer}>
              <Link to="/dried-fruits" className={styles.viewAll}>
                View All
              </Link>
            </div>
          </div>
          <div className={styles.row}>
            <h3 className={styles.rowTitle}>Spices</h3>
            {spices.map(product => (
              <Product
                category={product.category}
                name={product.name}
                origin={product.origin}
                price={product.price}
                unit={product.unit}
                thumb={product.thumb}
                slug={product.slug}
              />
            ))}
            <div className={styles.viewAllContainer}>
              <Link to="/spices" className={styles.viewAll}>
                View All
              </Link>
            </div>
          </div>
        </div>
        <div className={styles.bannerPageBottom}>
          <h2 className="h2">
            Visit Us Today and explore our range of products!
          </h2>
          <p className="subtitle">
            We are at{" "}
            <span className="address">
              Auburn Arcade, 12/75-77 Auburn Rd, Auburn NSW 2144
            </span>
          </p>
        </div>
      </section>
    </Layout>
  )
}

export const query = graphql`
  query Products {
    allDataJson {
      nodes {
        category
        id
        name
        origin
        price
        slug
        unit
        thumb {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
`
